import React, { useMemo } from "react"
import InfoWindow from "./InfoWindow"
import useMap from "../hooks/useMap"

export interface MapInfoWindowProps {
  /**
   * Contianer id  .
   */
  id?: string

  /**
   * Contianer className  .
   */
  className?: string

  /**
   * Contianer style  .
   */
  style?: React.CSSProperties

  /**
   *   
   */
  position: {
    lat: number
    lng: number
  }
  /**
   *         (: false)
   */
  disableAutoPan?: boolean

  /**
   *   
   */
  removable?: boolean

  /**
   *   z-index  
   */
  zIndex?: number

  /**
   *       
   */
  onCreate?: (infoWindow: kakao.maps.InfoWindow) => void
}

/**
 * Map  InfoWindow   .
 * `onCreate`     `infoWindow`      .
 */
const MapInfoWindow = React.forwardRef<
  kakao.maps.InfoWindow,
  React.PropsWithChildren<MapInfoWindowProps>
>(
  (
    { position, children, disableAutoPan, removable, zIndex, onCreate },
    ref
  ) => {
    const map = useMap(`MapInfoWindow`)
    const infoPosition = useMemo(() => {
      return new kakao.maps.LatLng(position.lat, position.lng)
    }, [position.lat, position.lng])

    return (
      <InfoWindow
        disableAutoPan={disableAutoPan}
        removable={removable}
        zIndex={zIndex}
        map={map}
        position={infoPosition}
        onCreate={onCreate}
        ref={ref}
      >
        {children}
      </InfoWindow>
    )
  }
)

export default MapInfoWindow
