import React, {
  useImperativeHandle,
  useLayoutEffect,
  useMemo,
  useRef,
} from "react"
import ReactDOM from "react-dom"
import useRoadview from "../hooks/useRoadview"

export interface CustomOverlayRoadviewProps {
  /**
   *   
   */
  position:
    | {
        lat: number
        lng: number
      }
    | {
        /**
         *  , 0 360     .
         */
        pan: number
        /**
         *  , -90 90     .
         */
        tilt: number
        /**
         *  , -3 3  .
         */
        zoom?: number
        /**
         *      
         */
        panoId?: number
      }
  /**
   * true         .
   */
  clickable?: boolean

  /**
   *  x . 0_1   .  0.5
   */
  xAnchor?: number

  /**
   *  y . 0_1   .  0.5
   */
  yAnchor?: number

  /**
   *   z-index
   */
  zIndex?: number

  /**
   *    () .
   *  m()        .
   */
  altitude?: number

  /**
   *    .
   *            .
   *  m()  500m.
   */
  range?: number

  /**
   *        callback   
   */
  onCreate?: (customOverlay: kakao.maps.CustomOverlay) => void
}

/**
 * Roadview CustomOverlay     .
 * `onCreate`   `ref`   `CustomOverlay`          .
 */
const CustomOverlayRoadview = React.forwardRef<
  kakao.maps.CustomOverlay,
  React.PropsWithChildren<CustomOverlayRoadviewProps>
>(
  (
    {
      position,
      children,
      clickable,
      xAnchor,
      yAnchor,
      zIndex,
      altitude,
      range,
      onCreate,
    },
    ref
  ) => {
    const roadview = useRoadview(`CustomOverlayRoadview`)
    const container = useRef(document.createElement("div"))

    const overlayPosition = useMemo(() => {
      if ("lat" in position) {
        return new kakao.maps.LatLng(position.lat, position.lng)
      }
      return new kakao.maps.Viewpoint(
        position.pan,
        position.tilt,
        position.zoom,
        position.panoId
      )
      /* eslint-disable react-hooks/exhaustive-deps */
    }, [
      // @ts-ignore
      position.lat,
      // @ts-ignore
      position.lng,
      // @ts-ignore
      position.pan,
      // @ts-ignore
      position.tilt,
      // @ts-ignore
      position.zoom,
      // @ts-ignore
      position.panoId,
    ])
    /* eslint-enable react-hooks/exhaustive-deps */

    const overlay = useMemo(() => {
      const KakaoCustomOverlay = new kakao.maps.CustomOverlay({
        clickable: clickable,
        xAnchor: xAnchor,
        yAnchor: yAnchor,
        zIndex: zIndex,
        position: overlayPosition,
        content: container.current,
      })
      container.current.style.display = "none"
      return KakaoCustomOverlay
      // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [clickable, xAnchor, yAnchor])

    useImperativeHandle(ref, () => overlay, [overlay])

    useLayoutEffect(() => {
      if (!roadview) return

      overlay.setMap(roadview)

      return () => {
        overlay.setMap(null)
      }
      // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [overlay, roadview])

    useLayoutEffect(() => {
      if (onCreate) onCreate(overlay)
    }, [overlay, onCreate])

    useLayoutEffect(() => {
      overlay.setPosition(overlayPosition)
    }, [overlay, overlayPosition])

    useLayoutEffect(() => {
      if (!zIndex) return
      overlay.setZIndex(zIndex)
    }, [overlay, zIndex])

    useLayoutEffect(() => {
      if (!altitude) return
      overlay.setAltitude(altitude)
    }, [overlay, altitude])

    useLayoutEffect(() => {
      if (!range) return
      overlay.setRange(range)
    }, [overlay, range])

    return (
      container.current.parentElement &&
      ReactDOM.createPortal(children, container.current.parentElement)
    )
  }
)

export default CustomOverlayRoadview
