import React, { useMemo } from "react"
import InfoWindow from "./InfoWindow"
import useRoadview from "../hooks/useRoadview"

export interface RoadviewInfoWindowProps {
  /**
   *   
   */
  position:
    | {
        lat: number
        lng: number
      }
    | {
        /**
         *  , 0 360     .
         */
        pan: number
        /**
         *  , -90 90     .
         */
        tilt: number
        /**
         *  , -3 3  .
         */
        zoom?: number
        /**
         *      
         */
        panoId?: number
      }

  /**
   *         (: false)
   */
  disableAutoPan?: boolean

  /**
   *   
   */
  removable?: boolean

  /**
   *   z-index  
   */
  zIndex?: number

  /**
   *     (m )
   */
  altitude?: number

  /**
   *    (m ),           
   */
  range?: number

  /**
   *       
   */
  onCreate?: (infoWindow: kakao.maps.InfoWindow) => void
}

/**
 * Map  InfoWindow   .
 * `onCreate`     `infoWindow`      .
 */
const RoadviewInfoWindow = React.forwardRef<
  kakao.maps.InfoWindow,
  React.PropsWithChildren<RoadviewInfoWindowProps>
>(
  (
    {
      position,
      children,
      altitude,
      disableAutoPan,
      range,
      removable,
      zIndex,
      onCreate,
    },
    ref
  ) => {
    const roadview = useRoadview(`RoadviewInfoWindow`)
    const infoPosition = useMemo(() => {
      if ("lat" in position) {
        return new kakao.maps.LatLng(position.lat, position.lng)
      }
      return new kakao.maps.Viewpoint(
        position.pan,
        position.tilt,
        position.zoom,
        position.panoId
      )
      /* eslint-disable react-hooks/exhaustive-deps */
    }, [
      // @ts-ignore
      position.lat,
      // @ts-ignore
      position.lng,
      // @ts-ignore
      position.pan,
      // @ts-ignore
      position.tilt,
      // @ts-ignore
      position.zoom,
      // @ts-ignore
      position.panoId,
    ])
    /* eslint-enable react-hooks/exhaustive-deps */

    return (
      <InfoWindow
        altitude={altitude}
        disableAutoPan={disableAutoPan}
        range={range}
        removable={removable}
        zIndex={zIndex}
        map={roadview}
        position={infoPosition}
        onCreate={onCreate}
        ref={ref}
      >
        {children}
      </InfoWindow>
    )
  }
)

export default RoadviewInfoWindow
