import React, { useImperativeHandle, useLayoutEffect, useMemo } from "react"
import useKakaoEvent from "../hooks/useKakaoEvent"
import useMap from "../hooks/useMap"

export interface EllipseProps {
  /**
   *   .
   */
  center: {
    lat: number
    lng: number
  }
  /**
   *   x 
   */
  rx: number

  /**
   *   y 
   */
  ry: number

  /**
   * #xxxxxx    (: #F10000)
   */
  fillColor?: string

  /**
   *   (0-1) (: 0)
   */
  fillOpacity?: number

  /**
   *     (: 3)
   */
  strokeWeight?: number

  /**
   * #xxxxxx    (: #F10000)
   */
  strokeColor?: string

  /**
   *   (0-1) (: 0.6)
   */
  strokeOpacity?: number

  /**
   *   (: solid)
   */
  strokeStyle?: kakao.maps.StrokeStyles

  /**
   *  z-index  
   */
  zIndex?: number

  /**
   *     .
   */
  onMouseover?: (
    target: kakao.maps.Ellipse,
    mouseEvent: kakao.maps.event.MouseEvent
  ) => void
  /**
   *     .
   */
  onMouseout?: (
    target: kakao.maps.Ellipse,
    mouseEvent: kakao.maps.event.MouseEvent
  ) => void
  /**
   *    .
   */
  onMousemove?: (
    target: kakao.maps.Ellipse,
    mouseEvent: kakao.maps.event.MouseEvent
  ) => void
  /**
   *     .
   */
  onMousedown?: (
    target: kakao.maps.Ellipse,
    mouseEvent: kakao.maps.event.MouseEvent
  ) => void
  /**
   *   .
   */
  onClick?: (
    target: kakao.maps.Ellipse,
    mouseEvent: kakao.maps.event.MouseEvent
  ) => void

  /**
   *    .
   */
  onCreate?: (target: kakao.maps.Ellipse) => void
}

/**
 * Map   .
 */
const Ellipse = React.forwardRef<kakao.maps.Ellipse, EllipseProps>(
  (
    {
      center,
      rx,
      ry,
      fillColor,
      fillOpacity,
      strokeColor,
      strokeOpacity,
      strokeStyle,
      strokeWeight,
      zIndex,
      onMouseover,
      onMouseout,
      onMousemove,
      onMousedown,
      onClick,
      onCreate,
    },
    ref
  ) => {
    const map = useMap(`Ellipse`)

    const ellipseCenter = useMemo(() => {
      return new kakao.maps.LatLng(center.lat, center.lng)
    }, [center.lat, center.lng])

    const ellipse = useMemo(() => {
      return new kakao.maps.Ellipse({
        center: ellipseCenter,
        rx,
        ry,
        fillColor,
        fillOpacity,
        strokeColor,
        strokeOpacity,
        strokeStyle,
        strokeWeight,
        zIndex,
      })
      // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [])

    useImperativeHandle(ref, () => ellipse, [ellipse])

    useLayoutEffect(() => {
      ellipse.setMap(map)

      return () => {
        ellipse.setMap(null)
      }
    }, [map, ellipse])

    useLayoutEffect(() => {
      if (onCreate) onCreate(ellipse)
    }, [ellipse, onCreate])

    useLayoutEffect(() => {
      if (ellipse) ellipse.setPosition(ellipseCenter)
    }, [ellipse, ellipseCenter])

    useLayoutEffect(() => {
      ellipse.setRadius(rx, ry)
    }, [ellipse, rx, ry])

    useLayoutEffect(() => {
      if (!zIndex) return
      ellipse.setZIndex(zIndex)
    }, [ellipse, zIndex])

    useLayoutEffect(() => {
      ellipse.setOptions({
        fillColor,
        fillOpacity,
        strokeColor,
        strokeOpacity,
        strokeStyle,
        strokeWeight,
      })
    }, [
      ellipse,
      fillColor,
      fillOpacity,
      strokeColor,
      strokeOpacity,
      strokeStyle,
      strokeWeight,
    ])

    useKakaoEvent(ellipse, "mouseover", onMouseover)
    useKakaoEvent(ellipse, "mouseout", onMouseout)
    useKakaoEvent(ellipse, "mousemove", onMousemove)
    useKakaoEvent(ellipse, "mousedown", onMousedown)
    useKakaoEvent(ellipse, "click", onClick)

    return null
  }
)

export default Ellipse
