import React, { useMemo } from "react"
import useRoadview from "../hooks/useRoadview"
import Marker from "./Marker"

export interface RoadviewMarkerProps {
  /**
   *  
   */
  position:
    | {
        lat: number
        lng: number
      }
    | {
        x: number
        y: number
      }
    | {
        /**
         *  , 0 360     .
         */
        pan: number
        /**
         *  , -90 90     .
         */
        tilt: number
        /**
         *  , -3 3  .
         */
        zoom?: number
        /**
         *      
         */
        panoId?: number
      }

  image?: {
    /**
     *   src
     */
    src: string

    /**
     *   
     */
    size: {
      width: number
      height: number
    }

    options?: {
      /**
       *   alt  .
       */
      alt?: string

      /**
       *        
       */
      coords?: string

      /**
       *       (:   )
       */
      offset?: { x: number; y: number }

      /**
       *       
       */
      shape?: "default" | "rect" | "circle" | "poly"

      /**
       *       
       */
      spriteOrigin?: { x: number; y: number }

      /**
       *    
       */
      spriteSize?: { width: number; height: number }
    }
  }

  /**
   * click  
   */
  onClick?: (marker: kakao.maps.Marker) => void

  /**
   * mouseover  
   */
  onMouseOver?: (marker: kakao.maps.Marker) => void

  /**
   * mouseout  
   */
  onMouseOut?: (marker: kakao.maps.Marker) => void

  /**
   * dragstart  
   */
  onDragStart?: (marker: kakao.maps.Marker) => void

  /**
   * dragend  
   */
  onDragEnd?: (marker: kakao.maps.Marker) => void

  /**
   * Maker   
   */
  onCreate?: (maker: kakao.maps.Marker) => void

  /**
   *      ()
   */
  title?: string

  /**
   *   
   */
  clickable?: boolean

  /**
   *   z-index  
   */
  zIndex?: number

  /**
   *   (0-1)
   */
  opacity?: number

  /**
   *     (m )
   */
  altitude?: number

  /**
   *    (m ),            .
   */
  range?: number

  /**
   * InfoWindow 
   */
  infoWindowOptions?: {
    /**
     *         (: false)
     */
    disableAutoPan?: boolean

    /**
     *   
     */
    removable?: boolean

    /**
     *   z-index  
     */
    zIndex?: number
  }
}

/**
 * Map Marker    .
 * `onCreate`     `maker`      .
 */
const RoadviewMarker = React.forwardRef<
  kakao.maps.Marker,
  React.PropsWithChildren<RoadviewMarkerProps>
>(
  (
    {
      image,
      position,
      children,
      altitude,
      clickable,
      infoWindowOptions,
      onClick,
      onDragEnd,
      onDragStart,
      onMouseOut,
      onMouseOver,
      onCreate,
      opacity,
      range,
      title,
      zIndex,
    },
    ref
  ) => {
    const roadview = useRoadview(`RoadviewMarker`)

    const markerImage = useMemo(() => {
      return (
        image &&
        new kakao.maps.MarkerImage(
          image.src,
          new kakao.maps.Size(image.size.width, image.size.height),
          {
            alt: image.options?.alt,
            coords: image.options?.coords,
            offset:
              image.options?.offset &&
              new kakao.maps.Point(
                image.options?.offset.x,
                image.options?.offset.y
              ),
            shape: image.options?.shape,
            spriteOrigin:
              image.options?.spriteOrigin &&
              new kakao.maps.Point(
                image.options?.spriteOrigin.x,
                image.options?.spriteOrigin.y
              ),
            spriteSize:
              image.options?.spriteSize &&
              new kakao.maps.Size(
                image.options?.spriteSize.width,
                image.options?.spriteSize.height
              ),
          }
        )
      )
    }, [image])

    const markerPosition = useMemo(() => {
      if ("lat" in position) {
        return new kakao.maps.LatLng(position.lat, position.lng)
      }
      if ("x" in position) {
        return new kakao.maps.Coords(position.x, position.y).toLatLng()
      }
      return new kakao.maps.Viewpoint(
        position.pan,
        position.tilt,
        position.zoom,
        position.panoId
      )

      /* eslint-disable react-hooks/exhaustive-deps */
    }, [
      // @ts-ignore
      position.lat,
      // @ts-ignore
      position.lng,
      // @ts-ignore
      position.x,
      // @ts-ignore
      position.y,
      // @ts-ignore
      position.pan,
      // @ts-ignore
      position.tilt,
      // @ts-ignore
      position.zoom,
      // @ts-ignore
      position?.panoId,
    ])
    /* eslint-enable react-hooks/exhaustive-deps */

    return (
      <Marker
        map={roadview}
        position={markerPosition}
        image={markerImage}
        altitude={altitude}
        clickable={clickable}
        infoWindowOptions={infoWindowOptions}
        onClick={onClick}
        onDragEnd={onDragEnd}
        onDragStart={onDragStart}
        onMouseOut={onMouseOut}
        onMouseOver={onMouseOver}
        onCreate={onCreate}
        opacity={opacity}
        range={range}
        title={title}
        zIndex={zIndex}
        ref={ref}
      >
        {children}
      </Marker>
    )
  }
)

export default RoadviewMarker
